import React from 'react';
import { Button, Typography } from '@mui/material';
import Image from 'next/image';
import ServiceIcon from '@mui/icons-material/Build'; // Ícone para prestador de serviço
import ClientIcon from '@mui/icons-material/Person'; // Ícone para cliente


const Home = () => {
    return (
        <div className={styles.container}>
            <Image
                src="/Logo.png" // Coloque o nome do arquivo da sua logomarca
                alt="Logomarca"
                width={200} // Ajuste o tamanho conforme necessário
                height={100} // Ajuste o tamanho conforme necessário
            />
            <Typography variant="h4" gutterBottom>
                Bem-vindo à Inova Mecânicas e Oficinas
            </Typography>
            <div className={styles.buttonContainer}>
                <Button
                    variant="contained"
                    color="primary"
                    startIcon={<ServiceIcon />}
                    onClick={() => console.log('Prestador de Serviço')}
                >
                    Sou Prestador de Serviço
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    startIcon={<ClientIcon />}
                    onClick={() => console.log('Cliente')}
                >
                    Sou Cliente
                </Button>
            </div>
        </div>
    );
};

export default Home;
